ts/DrukCyr-Medium.woff2') format('woff2');\n}\n\n@font-face {\n  font-family: 'EuclidCircularA';\n  font-weight: 400;\n  src: url('./assets/fonts/EuclidCircularA-Regular.woff') format('woff');\n}\n\n@font-face {\n  font-family: 'EuclidCircularA';\n  font-weight: 500;\n  src: url('./assets/fonts/EuclidCircularA-Medium.woff2') format('woff2');\n}\n\n@font-face {\n  font-family: 'EuclidCircularA';\n  font-weight: 600;\n  src: url('./assets/fonts/EuclidCircularA-SemiBold.woff2') format('woff2');\n}\n","// --------------------------------- mixins ---------------------------------\n\n@import './mixins';\n\n// -------------------------------- variables -------------------------------\n\n$font: 'EuclidCircularA';\n\n// colors\n$white: #ffffff;\n$black: #000000;\n$bodyColor: #1f1f22;\n$pink: #ed89fb;\n$violet: #cad2f4;\n$green: #c9fb40;\n$yellow: #fad85d;\n$darkGray: #6a6a6a;\n$main: #282828;\n$red: #ff1212;\n\n// ---------------------------------- fonts ---------------------------------\n\n// local fonts\n@import './fonts';\n\n// ------------------------------- base styles ------------------------------\n\n// base scss file\n@import './set';\n\n// body\nbody {\n    &.lock {\n        overflow: hidden;\n        touch-action: none;\n    }\n}\n\n// main\nmain {\n    padding-top: 6.7rem;\n    @media (max-width: 48em) {\n        main {\n            padding-top: 12.6rem;\n        }\n    }\n}\n\n// --------------------------------------------------------------------------\n\n// header / footer\n@import './components/_header';\n@import './components/_footer';\n\n// ui\n@import '../ui/ui.scss';\n\n// --------------------------------------------------------------------------\n\n@import './dev/vzmsk1.scss';\n@import './dev/markusDM.scss';\n@import './dev/ukik0.scss';\n@import './dev/kie6er.scss';\n","*,\n*::before,\n*::after {\n    box-sizing: border-box;\n}\nhtml {\n    font-family: 'EuclidCircularA'; //     \n    font-size: 0.5208335vw; //   1920 0.520835vw === 10px\n    font-style: normal;\n    font-weight: normal;\n    -webkit-animation: bugfix infinite 1s;\n    line-height: 1.2;\n    margin: 0;\n    padding: 0;\n}\n\nbody {\n    font-style: normal;\n    font-weight: normal;\n    -webkit-animation: bugfix infinite 1s;\n    line-height: 1.2;\n    margin: 0;\n    padding: 0;\n    color: $black; //      \n    background-color: $white;\n}\n\ninput,\ntextarea {\n    -webkit-animation: bugfix infinite 1s;\n    line-height: inherit;\n    margin: 0;\n    padding: 0;\n    background-color: transparent;\n    border: none;\n    color: inherit;\n}\na {\n    color: unset;\n}\na,\na:hover {\n    text-decoration: none;\n}\n\nbutton,\ninput,\na,\ntextarea {\n    outline: none;\n    cursor: pointer;\n    font: inherit;\n    &:focus {\n        outline: none;\n    }\n    &:active {\n        outline: none;\n    }\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n    font: inherit;\n    margin: 0;\n    padding: 0;\n}\np {\n    margin-top: 0;\n    margin-bottom: 0;\n}\n\nimg {\n    width: 100%;\n    height: auto;\n    display: block;\n}\n\nbutton {\n    border: none;\n    color: inherit;\n    font: inherit;\n    text-align: inherit;\n    padding: 0;\n    background-color: transparent;\n}\nul {\n    padding: 0;\n    margin: 0;\n}\n\nul li {\n    margin: 0;\n    padding: 0;\n    list-style: none;\n}\n\n.container {\n    width: 172rem;\n    margin: 0 auto;\n}\n\ninput[type='number']::-webkit-inner-spin-button,\ninput[type='number']::-webkit-outer-spin-button {\n    -webkit-appearance: none;\n    margin: 0;\n}\n\ninput[type='number'] {\n    -moz-appearance: textfield;\n}\n\n@media (min-width: 1920px) {\n    html {\n        font-size: 10px;\n    }\n}\n\n@media (max-width: 48em) {\n    html {\n        font-size: 5px;\n        font-size: 1.5625vw;\n        font-size: calc((100 / 375) * 5vw); //  375     \n        -webkit-text-size-adjust: none;\n    }\n\n    body {\n        -webkit-text-size-adjust: none;\n    }\n\n    .container {\n        padding: 0 4rem; //          ,        \n        width: 100%;\n    }\n}\n",".header {\n    position: fixed;\n    width: 100%;\n    top: 0;\n    left: 0;\n    z-index: 1000;\n\n    background-color: $bodyColor;\n    border-bottom: 0.1rem solid rgba(201, 251, 64, 0.5);\n    // .header__inner\n    &__inner {\n        display: flex;\n        align-items: center;\n        justify-content: space-between;\n        gap: 1rem;\n    }\n    // .header__burger-btn\n    &__burger-btn {\n        display: none;\n    }\n    // .header__logo\n    &__logo {\n        padding: 1rem 0;\n        height: 100%;\n        width: 22.7rem;\n        border-right: 0.1rem solid rgba(201, 251, 64, 0.5);\n    }\n    // .header__img\n    &__img {\n        width: 17.9878rem;\n        height: 4.6rem;\n    }\n    // .header__nav\n    &__nav {\n        display: flex;\n        gap: 5rem;\n        .nav__link {\n            font-family: EuclidCircularA;\n            color: $white;\n        }\n    }\n    &__burger-menu {\n        display: none;\n        overflow: auto;\n    }\n}\n\n@media (max-width: 48em) {\n    .header {\n        border-bottom: none;\n        &.scroll {\n            border-bottom: 0.1rem solid rgba(201, 251, 64, 0.5);\n        }\n        // .header__inner\n        &__inner {\n            display: flex;\n            align-items: center;\n            justify-content: space-between;\n            gap: 1rem;\n            padding: 1.6rem 0 1.8rem;\n        }\n        // .header__burger-btn\n        &__burger-btn {\n            display: flex;\n            width: 5.2rem;\n            height: 4.6rem;\n        }\n        // .header__logo\n        &__logo {\n            padding: 0;\n            height: 9.2rem;\n            width: 36.6rem;\n            border-right: none;\n        }\n        // .header__img\n        &__img {\n            width: 100%;\n            height: 100%;\n        }\n        // .header__nav\n        &__nav {\n            display: none;\n        }\n        // .header__phone\n        &__phone {\n            width: 4.8rem;\n            height: 4.8rem;\n        }\n    }\n}\n",".footer {\n    background-color: $yellow;\n    color: $main;\n    // .footer__inner\n    &__inner {\n        padding: 7rem 0;\n    }\n    // .footer__top\n    &__top {\n        margin-bottom: 8rem;\n    }\n    // .footer__sitemap\n    &__sitemap {\n        gap: 20rem;\n\n        .sitemap-item__link {\n            font-family: 'EuclidCircularA';\n        }\n    }\n    // .footer__middle\n    &__middle {\n        display: flex;\n        gap: 20rem;\n        align-items: flex-end;\n    }\n    // .footer__copyright\n    &__copyright {\n        color: $main;\n        font-size: 2rem;\n        width: 59.528rem;\n    }\n    // .footer__social\n    &__social {\n        gap: 1.6rem;\n    }\n    // .footer__info\n    &__info {\n        display: flex;\n        flex-direction: column;\n        margin-left: auto;\n        gap: 2.5rem;\n    }\n    // .footer__phone\n    &__phone {\n        color: $main;\n        font-size: 3.2rem;\n        line-height: normal;\n\n        &:hover,\n        &:focus-visible,\n        &:active {\n            text-decoration: underline;\n        }\n    }\n    // .footer__politic\n    &__politic {\n        color: $main;\n        font-size: 2rem;\n        line-height: normal;\n\n        position: relative;\n\n        &::after {\n            content: '';\n            width: 100%;\n            height: 0.15rem;\n            background-color: $main;\n\n            position: absolute;\n            left: 0;\n            bottom: 0;\n            z-index: 1;\n        }\n\n        &:hover,\n        &:focus-visible {\n            &::after {\n                display: none;\n            }\n        }\n    }\n    // .footer__bottom-text\n    &__bottom-text {\n        line-height: 0.86;\n        margin-top: 12rem;\n        margin-left: auto;\n        margin-right: auto;\n        text-align: center;\n        &.tl1 {\n            font-size: 19rem;\n        }\n    }\n}\n.footer-sitemap {\n    display: flex;\n    // .footer-sitemap__item\n    &__item {\n        &:nth-child(1) {\n            .sitemap-item__list {\n                grid-template-columns: repeat(3, 1fr);\n            }\n        }\n        &:nth-child(2) {\n            .sitemap-item__list {\n                grid-template-columns: 1fr;\n            }\n        }\n        &:nth-child(3) {\n            margin-left: auto;\n            .sitemap-item__list {\n                grid-template-columns: repeat(2, 1fr);\n            }\n        }\n    }\n}\n.sitemap-item {\n    // .sitemap-item__title\n    &__title {\n        margin-bottom: 3.2rem;\n    }\n    // .sitemap-item__list\n    &__list {\n        line-height: normal;\n        display: grid;\n        gap: 2rem 8rem;\n    }\n    // .sitemap-item__link\n    &__link {\n        line-height: inherit;\n        white-space: nowrap;\n        background: none;\n\n        &:hover,\n        &:focus-visible,\n        &:active {\n            text-decoration: underline;\n        }\n    }\n}\n\n@media (max-width: 48em) {\n    .footer {\n        background-color: $yellow;\n        color: $main;\n        // .footer__inner\n        &__inner {\n            padding: 10rem 0 17.8rem;\n        }\n        // .footer__top\n        &__top {\n            margin-bottom: 8.4rem;\n        }\n        // .footer__sitemap\n        &__sitemap {\n            padding-left: 4.4rem;\n            gap: 3rem;\n        }\n        // .footer__middle\n        &__middle {\n            display: flex;\n            flex-direction: column;\n            gap: 2rem;\n            align-items: center;\n        }\n        // .footer__copyright\n        &__copyright {\n            order: 2;\n            text-align: center;\n            font-size: 2.4rem;\n            width: 100%;\n        }\n        // .footer__social\n        &__social {\n            gap: 6rem;\n            order: 3;\n            margin-top: 5.6rem;\n        }\n        // .footer__info\n        &__info {\n            text-align: center;\n            display: flex;\n            flex-direction: column;\n            margin-left: 0;\n            gap: 1rem;\n            order: 1;\n        }\n        // .footer__phone\n        &__phone {\n            font-size: 3.6rem;\n        }\n        // .footer__politic\n        &__politic {\n            color: $main;\n            font-size: 3rem;\n\n            position: relative;\n\n            &::after {\n                display: none;\n            }\n        }\n        // .footer__bottom-text\n        &__bottom-text {\n            text-align: center;\n            line-height: 1.1;\n            margin-top: 10.4rem;\n        }\n    }\n    .footer-sitemap {\n        display: grid;\n        grid-template-columns: repeat(2, 28rem);\n        grid-auto-flow: dense;\n        // .footer-sitemap__item\n        &__item {\n            &:nth-child(1) {\n                grid-row: span 2;\n            }\n            &:nth-child(3) {\n                margin-left: 0;\n            }\n        }\n    }\n    .sitemap-item {\n        // .sitemap-item__title\n        &__title {\n            margin-bottom: 2rem;\n        }\n        // .sitemap-item__list\n        &__list {\n            line-height: normal;\n            display: flex;\n            flex-direction: column;\n            gap: 1.6rem;\n        }\n        // .sitemap-item__link\n        &__link {\n            line-height: inherit;\n            white-space: nowrap;\n            background: none;\n\n            &:hover,\n            &:focus-visible,\n            &:active {\n                text-decoration: underline;\n            }\n        }\n    }\n}\n",".tl1,\n.tl2,\n.tl3 {\n  font-family: DrukCyr;\n  font-weight: 500;\n  text-transform: uppercase;\n}\n\n.tl1 {\n  font-size: 20rem;\n  line-height: 117%;\n  @media (max-width: 48em) {\n    font-size: 11rem;\n    line-height: 110%;\n  }\n}\n\n.tl2 {\n  line-height: 110%;\n  font-size: 10rem;\n}\n\n.tl3 {\n  font-size: 6rem;\n  line-height: 110%;\n  @media (max-width: 48em) {\n    font-size: 4rem;\n    