: 3rem;\n    }\n  }\n}\n\ntextarea.input {\n  padding: 0;\n  resize: none;\n}\n\n.form-error {\n  position: absolute;\n  top: calc(100% + 0.9rem);\n  left: 0;\n  color: $red;\n  @media (max-width: 48em) {\n    top: calc(100% + 1.8rem);\n  }\n}\n\n// <div class=\"input\">\n//           <input\n//             autocomplete=\"off\"\n//             type=\"text\"\n//             name=\"form[]\"\n//             data-error=\"\"\n//             placeholder=\" *\"\n//             class=\"input__field txt20 txt20_md\" />\n",".tabs {\n  // .tabs__navigation\n\n  &__navigation {\n    display: flex;\n    column-gap: 2rem;\n  }\n\n  // .tabs__title\n\n  &__title {\n  }\n\n  // .tabs__content\n\n  &__content {\n  }\n\n  // .tabs__body\n\n  &__body {\n  }\n}\n\n.tab {\n  padding: 1.6rem 3.3rem 1.9rem 3.3rem;\n  display: inline-flex;\n  justify-content: center;\n  align-items: center;\n  border: 1px solid $black;\n  border-radius: 4rem;\n  text-align: center;\n  transition: background-color 0.3s ease;\n  &._active {\n    background-color: $green;\n  }\n  @media (max-width: 48em) {\n    padding: 1.8rem 5rem;\n  }\n}\n\n// <div data-tabs class=\"tabs\">\n// <nav data-tabs-titles class=\"tabs__navigation\">\n//   <button type=\"button\" class=\"tabs__title txt20 txt20_caps tab _active\">\n//      1\n//   </button>\n//   <button type=\"button\" class=\"tabs__title txt20 txt20_caps tab\"> 2</button>\n//   <button type=\"button\" class=\"tabs__title txt20 txt20_caps tab\"> 3</button>\n// </nav>\n// <div data-tabs-body class=\"tabs__content\">\n//   <div class=\"tabs__body\">  </div>\n//   <div class=\"tabs__body\">  </div>\n//   <div class=\"tabs__body\">  </div>\n// </div>\n// </div>\n",".accordion {\n  // .accordion__item\n\n  &__item {\n    border-radius: 4rem;\n    background-color: $green;\n  }\n\n  // .accordion__title\n\n  &__title {\n    padding: 3.2rem 3.2rem 3.2rem 4rem;\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    width: 100%;\n    &._accordion-active {\n      .accordion__title-icon {\n        transform: rotate(-180deg);\n      }\n    }\n    @media (max-width: 48em) {\n      padding: 4rem;\n    }\n  }\n\n  // .accordion__title-icon\n\n  &__title-icon {\n    flex: 0 0 5rem;\n    width: 5rem;\n    height: 5rem;\n    transition: transform 0.3s ease;\n    @media (max-width: 48em) {\n      flex: 0 0 8rem;\n      width: 8rem;\n      height: 8rem;\n    }\n  }\n\n  // .accordion__title-txt\n\n  &__title-txt {\n  }\n\n  // .accordion__body\n\n  &__body {\n    padding: 4rem 3.2rem;\n    padding-top: 0;\n    @media (max-width: 48em) {\n      padding: 4rem;\n      padding-top: 0;\n    }\n  }\n\n  // .accordion__text\n\n  &__text {\n    font-family: EuclidCircularA;\n    max-width: 72.6rem;\n    @media (max-width: 48em) {\n      max-width: 62.2rem;\n    }\n  }\n}\n\n// <div data-accordion data-accordion-one-active class=\"accordion\">\n//           <div class=\"accordion__item\">\n//             <button type=\"button\" data-accordion-item class=\"accordion__title\">\n//               <svg\n//                 class=\"accordion__title-icon\"\n//                 width=\"52\"\n//                 height=\"52\"\n//                 viewBox=\"0 0 52 52\"\n//                 fill=\"none\"\n//                 xmlns=\"http://www.w3.org/2000/svg\">\n//                 <circle cx=\"26\" cy=\"26\" r=\"26\" fill=\"#1F1F22\" />\n//                 <path\n//                   d=\"M18.872 21L33 21L25.936 31.3605L18.872 21Z\"\n//                   fill=\"white\" />\n//               </svg>\n//               <span class=\"accordion__title-txt tl3\"></span>\n//             </button>\n//             <div class=\"accordion__body\">\n//               <p class=\"accordion__text txt20\">\n//                       \n//                    ,     \n//                   .\n//               </p>\n//             </div>\n//           </div>\n//         </div>\n",".select {\n    position: relative;\n    @media (min-width: 48em) {\n        min-width: 20.4rem;\n    }\n\n    // .select__body\n\n    &__body {\n        position: relative;\n    }\n\n    // .select__title\n\n    &__title {\n        border-radius: 4rem;\n        width: 100%;\n        background-color: $violet;\n        cursor: pointer;\n        text-align: left;\n        color: inherit;\n    }\n\n    // .select__value\n\n    &__value {\n        @extend .txt20_caps;\n        padding: 1.4rem 2.4rem;\n        display: flex;\n        justify-content: space-between;\n        align-items: center;\n        gap: 1rem;\n        height: 5.2rem;\n        width: 100%;\n        font-family: DrukCyr;\n\n        > * {\n            flex: 1 1 auto;\n        }\n\n        &::after {\n            content: '';\n            flex: 0 0 2.4rem;\n            width: 2.4rem;\n            height: 2.4rem;\n            background-image: url(./assets/images/icons/arr.svg);\n            background-size: contain;\n            background-position: center;\n            background-repeat: no-repeat;\n            transition: transform 0.3s ease;\n            ._select-opened & {\n                transform: rotate(-180deg);\n            }\n        }\n        .select__content {\n            max-width: 31.4rem;\n            overflow: hidden;\n            white-space: nowrap;\n            text-overflow: ellipsis;\n        }\n        @media (max-width: 48em) {\n            padding: 1.8rem 4rem;\n            height: 8rem;\n            &::after {\n                flex: 0 0 5rem;\n                width: 5rem;\n                height: 5rem;\n            }\n        }\n    }\n\n    // .select__content\n\n    &__content {\n        // hide / show selected value\n        // display: none;\n    }\n\n    // .select__text\n\n    &__text {\n        flex: 1 1 auto;\n    }\n\n    // .select__input\n\n    &__input {\n        width: 100%;\n        height: 100%;\n        background-color: transparent;\n    }\n\n    // .select__options\n\n    &__options {\n        position: absolute;\n        z-index: 2;\n        top: calc(100% + 0.4rem);\n        left: 0;\n        padding: 1.2rem 2.8rem 2rem 1.2rem;\n        min-width: 100%;\n        box-shadow: 0 0.8rem 2.5rem 0 rgba(99, 119, 156, 0.2);\n        border-radius: 4rem;\n        background-color: $violet;\n        opacity: 0;\n        visibility: hidden;\n        transition: opacity 0.3s ease, visibility 0.3s ease;\n        .dropdown_sort & {\n            background-color: $white;\n        }\n        ._select-opened & {\n            opacity: 1;\n            visibility: visible;\n        }\n    }\n\n    // .select__scroll\n\n    &__scroll {\n        overflow-y: auto;\n        overflow-x: hidden;\n\n        // maximum height\n        max-height: 23.4rem;\n\n        // scrollbar styles\n        &.simplebar-scrollable-y {\n            .simplebar-track.simplebar-vertical {\n                top: 5rem;\n                right: 0;\n                width: 0.4rem;\n                border-radius: 0.8rem;\n                background-color: rgba(255, 255, 255, 0.4);\n            }\n            .simplebar-scrollbar {\n                min-height: 3.2rem;\n                border-radius: 0.8rem;\n                background-color: $white;\n            }\n        }\n        @media (max-width: 48em) {\n            max-height: 40rem;\n        }\n    }\n    // .select__option\n    &__option {\n        @extend .txt20_caps;\n        width: 95%;\n        padding: 1.2rem 3rem;\n        border-radius: 4rem;\n        font-family: DrukCyr;\n        text-align: left;\n        color: inherit;\n        transition: background-color 0.3s ease;\n        &._select-selected {\n            background-color: $white;\n            .dropdown_sort & {\n                background-color: $violet;\n            }\n        }\n        &:not(:last-child) {\n            position: relative;\n            margin-bottom: 1rem;\n            &::after {\n                content: '';\n                position: absolute;\n                top: calc(100% + 0.5rem);\n                left: 0;\n                width: 100%;\n                height: 1px;\n                background-color: rgba(204, 213, 251, 1);\n            }\n        }\n        @media (any-hover: hover) {\n            &:hover {\n                &:not(&.select__subtitle) {\n                    cursor: pointer;\n                    background-color: $white;\n                    .dropdown_sort & {\n                        background-color: $violet;\n                    }\n                }\n            }\n        }\n    }\n}\n// list\n._select-list {\n    cursor: pointer;\n}\n\n// <div class=\"dropdown dropdown_filters\">\n// <select data-no-slide data-show-selection data-sel-scroll=\"234\">\n//     <option value=\"1\" selected> 1</option>\n//     <option value=\"2\"> 2</option>\n//     <option value=\"3\"> 3</option>\n//     <option value=\"4\"> 4</option>\n//     <option value=\"5\"> 5</option>\n//     <option value=\"6\"> 6</option>\n//     <option value=\"7\"> 7</option>\n// </select>\n// </div>\n\n// <div class=\"dropdown dropdown_sort\">\n//      <select data-show-selection>\n//           <option value=\"1\" selected> 1</option>\n//           <option value=\"2\"> 2</option>\n//           <option value=\"3\"> 3</option>\n//           <option value=\"4\"> 4</option>\n//      </select>\n// </div>\n",".option {\n    position: relative;\n    display: flex;\n    flex-direction: column;\n    row-gap: 0.4rem;\n    cursor: pointer;\n\n    // .option__input\n    &__input {\n        position: absolute;\n        width: 0;\n        height: 0;\n        opacity: 0;\n        &:checked + .option__label .option__text::before {\n            border: 2px solid $green;\n        }\n        &:checked + .option__label .option__text::after {\n            transform: scale(0.6);\n        }\n    }\n\n    // .option__text\n    &__text {\n        display: inline-flex;\n        align-items: center;\n        gap: 1rem;\n        cursor: pointer;\n        &::before {\n            content: '';\n            align